// background color swipe in top bottom out

.mmu-preloader-2 {
    position: fixed;
    width: 100vw;
    height: 100vh;
    background-color: $secondary;
    left: 0;
    z-index: 9999999;
    top: -0;
    

    .falling-div {
        width: 100%;
        height: 100%;
        display: flex;
        flex-wrap: nowrap;
        justify-content: space-around;
        align-items: center;

        .navbar-brand {
            max-width: 7em;
        }
    }
}

.preloader.loaded {
    animation: preloader 3s ease-in-out forwards;
}



@keyframes preloader {
    to {
        top: 150vh;
        display: none;
    }
}

//fallback
.preloader.no-js {
    display: none;
}